import React from 'react';

function Input({send, input}){
    return(
        <div className="position">    
            <input type="text" ref={input} className="input" placeholder="Введите сообщение"></input>        
            <button onClick={send} className='send'><i class="fa fa-paper-plane" aria-hidden="true"></i></button>
        </div>
    )
}

export default Input;