import React from 'react';

function Messages({item,del, dark}){
    return(
        <div className={dark? 'nmessages' : 'messages'}>
            <div className="avatar">
                <img src={item.author.avatar} alt='avatar' className="img"></img>
            </div>
            <div className="message-cont">
                <div className="name">
                    {item.author.name}
                </div>
                <div className="message">
                    {item.message}
                </div>
                <div className="del">
                    <button onClick={()=>del(item.id)} className={dark? 'ndelbutton' : 'delbutton'}>
                        Удалить
                    </button>
                </div>
            </div>
            <div className="time">
                {item.time}
            </div>
        </div>
    )
}

export default Messages;