import React from 'react';

function Header({dark, set}){
    return(
        <div className={dark? 'nheader' : 'header'}>
           <div>Hi, intocode</div>
           <div><button className={dark? 'nnight' : 'night'} onClick={set}>Ночной режим</button></div>
        </div>
    )
};

export default Header;