import React, { useState, useRef } from 'react';
import Header from './Header';
import Content from './Content';
import Input from './Input';

function App(){

const database = {
    username: "intocode",
    darkTheme: false,
  
    messages: [
      {
        id: 1,
        message: "Привет, как дела?",
        author: {
          name: "intocode",
          avatar: "http://intocode.ru/d/react-chat/avatars/admin.jpg"
        },
        time: "11:42",
      },
      {
        id: 2,
        message: "Подскажите, как можно удалить JavaScript из мозга?",
        author: {
          name: "shovda",
          avatar: "http://intocode.ru/d/react-chat/avatars/no-avatar.jpg"
        },
        time: "13:21",
      },
      {
        id: 3,
        message: "Тоже хотел бы знать",
        author: {
          name: "intocode",
          avatar: "http://intocode.ru/d/react-chat/avatars/admin.jpg"
        },
        time: "13:22",
      },
      {
        id: 4,
        message: "Никак :(",
        author: {
          name: "adam",
          avatar: "http://intocode.ru/d/react-chat/avatars/adam.jpg"
        },
        time: "13:23",
      },
      {
        id: 5,
        message: "В интукод сложные задания. Больше к ним не пойду.",
        author: {
          name: "coronavirus",
          avatar: "http://intocode.ru/d/react-chat/avatars/coronavirus.png"
        },
        time: "14:00",
      },
      {
        id: 6,
        message: "А что еще делать? Карантин продлится до 2032г.",
        author: {
          name: "na_karantine",
          avatar: "http://intocode.ru/d/react-chat/avatars/no-avatar.jpg"
        },
        time: "14:04",
      },
      {
        id: 7,
        message: "Лайкните последний пост на instagram.com/intocode",
        author: {
          name: "intocode",
          avatar: "http://intocode.ru/d/react-chat/avatars/admin.jpg"
        },
        time: "15:10",
      },
      {
        id: 8,
        message: "Отстань.",
        author: {
          name: "adam",
          avatar: "http://intocode.ru/d/react-chat/avatars/adam.jpg"
        },
        time: "15:12",
      },
      {
        id: 9,
        message: ":(",
        author: {
          name: "intocode",
          avatar: "http://intocode.ru/d/react-chat/avatars/admin.jpg"
        },
        time: "15:13",
      }
    ]
  };
  const input = useRef(null);
  const [base, setBase] = useState(database.messages);
  const [dark, setDark] = useState(database.darkTheme);

  const body = document.body;
  body.className = dark? 'nbody':'body';

  const delMessage = id =>{
    setBase(base.filter(item =>{
      if (id === item.id){
        return false;
      }
      return true
    }))

  }

  

  const newMessage = ()=>{
      const newList = [
          ...base,
          {
              id: base.length,
              message: input.current.value,
              author: {
                  name: 'Yusup',
                  avatar: "http://intocode.ru/d/react-chat/avatars/admin.jpg"
              },
              time:`${new Date().getHours()}:${new Date().getMinutes()}`,
          }
      ]
      input.current.value = '';
      setBase(newList)
  }

  const turnNight = ()=>{
    setDark(!dark)
  }

    return(
        <div className='app'>
            <Header dark={dark} set={turnNight}/>
            <Content base={base} del={delMessage} dark={dark}/>
            <Input send={newMessage} input={input}/>
        </div>
    )
}

export default App;